import { HubConnectionBuilder } from "@aspnet/signalr";

const GameHub = function(hubUrl, onReceiveMessageFn, onUpdateFn) {

    var connection = undefined;
    var currentUserName = undefined;
    var currentRoomId = undefined;

    async function openConnection() {

        connection = new HubConnectionBuilder()
            .withUrl(hubUrl)
            .build();

        connection.on("ReceiveMessage", onReceiveMessageFn);
        connection.on("Update", onUpdateFn);

        await connection.start();
    }

    async function joinRoom(roomId, userName) {

        currentRoomId = roomId;
        currentUserName = userName;

        await openConnection();

        connection.invoke("JoinRoom", roomId, userName);
    }


    return {
        joinRoom: joinRoom,

        sendMessage(text) {
            connection.invoke("Send", currentRoomId, currentUserName, text);
        },
        startNewGame() {
            connection.invoke("ProcessEvent", currentRoomId, currentUserName, "StartNewGame", []);
        },
        getFromDeck() {
            connection.invoke("ProcessEvent", currentRoomId, currentUserName, "GetFromDeck", []);
        },
        getFromBoard() {
            connection.invoke("ProcessEvent", currentRoomId, currentUserName, "GetFromBoard", []);
        },
        fromHandToBoard(card) {
            connection.invoke("ProcessEvent", currentRoomId, currentUserName, "FromHandToBoard", [card]);
        },
        clearBoardPastHistory() {
            connection.invoke("ProcessEvent", currentRoomId, currentUserName, "ClearBoardPastHistory", []);
        }
    };
}

export default GameHub;